.braft-editor {
  border: 1px solid #ddd;
  border-radius: 5px;
  .bf-controlbar{
    .separator-line{
      height: 0;
      margin: 0;
    }
    .control-item{
      margin: 0px 0 0px 3px;
      height: auto;
      &.button{
        margin: 0;
        padding: 0;
        min-width: 30px;
      }
    }
  }
  .bf-content{
    height: auto;
    min-height: 400px;
    padding-bottom: 0;
  }
}
